@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Qwitcher+Grypen&display=swap);
body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a:-webkit-any-link {
	text-decoration: none !important;	
}



a{
	color:#fff !important;
}

.dropdown-item{
	color:#000 !important;
}

.btn-link {
	color:#fff !important;
}
.itemsNavbar{
	color: #fff;
	margin-right: 2%;
	display: flex;
	flex-direction: row;
	flex-wrap: nowrap;
	align-items: flex-end;
	justify-content: flex-end;
}

a :active{
	padding: 5px;
	border-radius: 20px;
	border: 3px solid white;
}


.navbar-nav {
    display: flex !important;
    flex-direction: row !important;
    padding-left: 0 !important;
    margin-bottom: 0 !important;
    list-style: none !important;
    flex-wrap: nowrap !important;
}


.navbar-toggler:focus {	
	box-shadow: none;
}
	

* {
  font-family: "Poppins", sans-serif;
  font-style: normal;
}

.textcolor {
  color: white;
}

.h-title {
  white-space: nowrap;
  font-size: 200%;
  font-weight: 700;
}

.h-subtitle {
  font-size: 100%;
}

.p-title {
  font-size: 150%;
  
}

.bold{
  font-weight: 700;
}


.p-subtitle {
  font-size: 70%;
}

.centralized {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.alignleft {
  text-align: left;
}

#indicator {
  position: top;
  margin-top: 0px;
  margin-bottom: 10px;
  left: 5%;
  bottom: 0;
  width: 20%;
  height: 1%;
  background-color: #fe9759;
  border-radius: 5px;
}


#indicator2{
  margin: auto; 
  width: 10%;
  height: 8%;
  background-color: #fe9759;
  border-radius: 5px;
}

.assignature {
	text-align: left;
	font-size: 300%;
	font-weight: 400;
	font-family: 'Qwitcher Grypen', cursive;
}

body {
    background: url(/static/media/BG_top.f7e9690c.png) no-repeat 0% 0%;
    background-size: 100%;
    text-align: center;
    vertical-align: center;
    height: 100vh; 
}

.bannerhistory{
    background-color: white;
    width: 100%;
    height: 80%;
    margin: auto;
    margin-top: 20px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    overflow-y: hidden !important; 
}


@media screen and (min-width: 480px) {
    .bannerhistory{
        width: 100%;
        height: auto;
    }
    .cover{
        width: 50%;
        height: 50%;

    }
}

.sizebox{
    margin-right: 10%;
}

.cover{
    margin-top: 5%;
    margin-bottom: 5%;
    height: 95%;
    width: 100%;
    object-fit: fill;
}





    


.bg {
  background: url(/static/media/BG2.1b848726.png) no-repeat 0% 0%;
  background-size: 100%;
  text-align: center;
  vertical-align: center;
  width: 100%;
  height: 100%;
}

.imagOffers {
  width: 100%;
  height: 70%;
}

.cardOffers {
  padding: 0;
  height: 100%;
  width: 250px;
  left: 290px;
  top: 0px;
  border-radius: 0px;

  background: #FFFFFF;
  box-shadow: 0px 0px 12px 21px rgba(0, 0, 0, 0.25);
}

.retangle {
  height: 75px;
  width: 67px;
  background: url(/static/media/retangle.f7d9d1b1.svg) no-repeat 0% 0%;
  background-size: 100%;
  position: absolute;
}


  
  


