body {
    background: url("./img/BG_top.png") no-repeat 0% 0%;
    background-size: 100%;
    text-align: center;
    vertical-align: center;
    height: 100vh; 
}

.bannerhistory{
    background-color: white;
    width: 100%;
    height: 80%;
    margin: auto;
    margin-top: 20px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    overflow-y: hidden !important; 
}


@media screen and (min-width: 480px) {
    .bannerhistory{
        width: 100%;
        height: auto;
    }
    .cover{
        width: 50%;
        height: 50%;

    }
}

.sizebox{
    margin-right: 10%;
}

.cover{
    margin-top: 5%;
    margin-bottom: 5%;
    height: 95%;
    width: 100%;
    object-fit: fill;
}





    

